import React from 'react'
import Head from 'next/head'
import { ThemeProvider, StylesProvider } from '@material-ui/styles'
import CssBaseline from '@material-ui/core/CssBaseline'
import theme from '../theme'
import { PageTransition } from 'next-page-transitions'
import { DefaultSeo } from 'next-seo'
import seoConfig from '../../next-seo.config'

import 'animate.css'

const DEFAULT_SEO = () => (
    <DefaultSeo
        title="StraD Home"
        description="web site of Sho Tanaka who is hair artist and web programer "
        keywords="hair make,hair Style,programing,react,next.js,"
        {...seoConfig}
    />
)

export default function StradApp({
    images,
    blogs,
    Component,
    pageProps,
    router,
}) {
    React.useEffect(() => {
        // Remove the server-side injected CSS.
        const jssStyles = document.querySelector('#jss-server-side')
        if (jssStyles) {
            jssStyles.parentNode.removeChild(jssStyles)
        }
    }, [])

    return (
        <>
            <Head>
                <DEFAULT_SEO />
                <title>StraD inc.</title>
            </Head>
            <StylesProvider injectFirst>
                <ThemeProvider theme={theme}>
                    <CssBaseline />
                    <PageTransition timeout={100} classNames="page-transition">
                        <Component
                            {...pageProps}
                            key={router.route}
                            images={images}
                            blogs={blogs}
                        />
                    </PageTransition>
                    <style jsx global>{`
                        .page-transition-enter {
                            opacity: 0;
                        }
                        .page-transition-enter-active {
                            opacity: 1;
                            transition: opacity 200ms;
                        }
                        .page-transition-exit {
                            opacity: 1;
                        }
                        .page-transition-exit-active {
                            opacity: 0;
                            transition: opacity 200ms;
                        }
                    `}</style>
                </ThemeProvider>
            </StylesProvider>
        </>
    )
}

export async function getStaticProps() {
    const key = {
        headers: { 'X-API-KEY': process.env.API_KEY },
    }
    const imageRes = await fetch(
        `${process.env.API_END_POINT}/image?limit=30`,
        key
    )
    const blogRes = await fetch(`${process.env.API_END_POINT}/blogs`, key)
    const imageData = await imageRes.json()
    const blogData = await blogRes.json()

    return {
        props: {
            images: imageData.contents,
            blogs: blogData.contents,
        },
    }
}
